@font-face {
  src: url(/static/media/Inter-Black.d0b121f3.woff) format('woff'),
       url(/static/media/Inter-Black.661569af.woff2) format('woff2');
  font-family: Inter;
  font-weight: 900;
}

@font-face {
  src: url(/static/media/Inter-Medium.66f0ef61.woff) format('woff'),
       url(/static/media/Inter-Medium.eea97ded.woff2) format('woff2');
  font-family: Inter;
  font-weight: 500;
}

@font-face {
  src: url(/static/media/Inter-Regular.3ac83020.woff) format('woff'),
	   url(/static/media/Inter-Regular.dc131113.woff2) format('woff2');
  font-family: Inter;
  font-weight: 400;
}

.page {
  max-width: 880px;
  margin: auto;
  font-family: Inter, 'Helvetica Neue', Arial, sans-serif;
  padding: 0 19px;
}

@media screen and (max-width: 670px) {
  .page {
    padding: 0 19px;
  }
}

.body {
  background-color: rgb(237 238 240)
}


.content {

  margin: auto;

}

.header {
  border-bottom: solid 1px rgba(84,84,84, 0.7);
  display: flex;
  max-width: 880px;
  margin: 0 auto;
  justify-content: space-between;
  color: #07294e;;
}

@media screen and (max-width: 470px) {
  .header {
    flex-direction: column;
    align-items: center;
  }
}





.header__logo {
  margin-top: 45px;
  width: 142px;
  height: 33px;
  margin-bottom: 41px;
  border-radius: 7px;
}

@media screen and (max-width: 670px) {
  .header__logo {
    margin-top: 28px;
    width: 103px;
    height: 24px;
    margin-left: 8px;
    margin-bottom: 31px;
    padding: 0 19px;
  }
}

.header__info {
    font-size: 18px;
    line-height: 22px;
    box-sizing: border-box;
    align-items: center;
    display: flex;
  }
.header__link {
    margin: 0;
    text-decoration: none;
    color: #07294e;
    align-self: center;
  }

  .header__link:hover {
    opacity: 0.6;
    transition: 0.3s;
  }

.header__email {
   margin: 0 24px 0 0; 
  }
.login{
  max-width: 358px;
  width: 100%;
  margin: 60px auto 112px;
  padding: 0;
  position: relative;
}

@media (max-width: 320px) {
    .login {
      max-width: 260px;
    }
  }

.login__title {
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    color: #07294e;
    margin-bottom: 50px;
    text-align: center;
}

@media (max-width: 320px) {
    .login__title {
      margin-bottom: 40px;
    }
  }

.login__input {
    border: none;
    outline: none;
    width: 100%;
    border-bottom: 2px solid rgba(	204, 204, 204, 1);
    margin-bottom: 30px;
    background: none;
    box-sizing: border-box;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    color: #3b3b3b;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
}

.login__submit-button {
  margin: 150px 0 0 0;
  height: 50px;
  color: white;
  border-radius: 10px;
  padding: 0;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  background: #07294e;
  transition: 0.3s;
  width: 100%;
  border: none;
}

.login__submit-button:disabled {
  background-color: rgba(7,41,78, 0.5);
}

.login__submit-button:hover {
  opacity: 0.8;
  cursor: pointer;
}

@media (max-width: 400px) {
  .login__submit-button {
    margin: 143px 0 0 0;
    height: 46px;
    font-size: 15px;
  }
}

.login__link-enter {
  text-decoration: none;
  transition: 0.3s;
  font-size: 14px;
  line-height: 17px;
  color: #07294e;
  cursor: pointer;
  display: flex;
  justify-content: center;
}

.login__link-enter:hover {
  opacity: 0.6;
}


@media (max-width: 400px) {
  .login__link-enter {
    font-size: 10px;
  }
}

.login__input-error {
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  color: rgba(255, 0, 0, 0.8);
  position: absolute;
  margin-top: 23px;
  left: 0;
}

.footer__description {
  margin-top: 67px;
  margin-bottom: 60px;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: #07294e;
}

@media screen and (max-width: 670px) {
  .footer__description {
    margin-top: 48px;
    font-size: 14px;
    line-height: 17px;
    padding: 0 19px;
    margin-bottom: 36px;
  }
}

.element {
  background: #FFFFFF;
  border-radius: 10px;
  overflow: hidden;
  position: relative;
}

.element__caption {
  display: flex;
  margin: 25px 15px 25px 21px;
  justify-content: space-between;
}

.element__image {
  width: 100%;
  height: 300px;
  object-fit: contain;
  cursor: pointer;
  font-size: 11px;
  color: rgb(136, 136, 136);
}

.element__like {
  cursor: pointer;
  transition: 0.3s;
  background-image: url(/static/media/like.eec224cb.svg);
  width: 22px;
  height: 19px;
  border: none;
  background-color: transparent;
  align-self: center;
  background-repeat: no-repeat;
  padding: 0px;
  background-size: 22px 19px;
  background-position: center;
}

.element__like:hover {
  opacity: 0.6;
  transition: 0.3s;
}

.element__like-counter {
  margin-top: 3px;
  text-align: center;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
}

.element__delete {
  cursor: pointer;
  transition: 0.3s;
  background-image: url(/static/media/Delete_Icon.4831282e.svg);
  width: 30px;
  height: 30px;
  border: none;
  position: absolute;
  bottom: 5px;
  right: 10px;
  background-repeat: no-repeat;
  background-position: center;
  background-color: #07294e;
  border-radius: 8px;
}

.element__delete:hover {
  opacity: 0.6;
  transition: 0.3s;
}

.element__delete_disabled {
  display: none;
}

.element__like_active {
  background-image: url(/static/media/like_active.3d813f81.svg);
}

.element__name {
  margin: 0px;
  font-weight: 900;
  font-size: 18px;
  line-height: 29px;
  text-overflow: ellipsis;
  overflow: hidden;
  max-width: 90%;
  overflow-wrap: anywhere;
}

.elements {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(282px, 80%));
  grid-template-rows: auto;
  grid-gap: 17px;
  gap: 17px;
  justify-content: center;
}

.popup {
  visibility: hidden;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  justify-content: center;
  align-items: center;
  opacity: 40%;
  transition: 0.5s opacity, 0.2s visibility;
  display: flex;
}

.popup__close-button {
  background-image: url(/static/media/Close_Icon.cb0f2bbf.svg);
  position: absolute;
  right: -40px;
  top: -42px;
  width: 32px;
  height: 32px;
  transition: 0.3s;
  cursor: pointer;
  background-color: transparent;
  border: none;
  padding: 0px;
  background-size: 32px 32px;
  background-repeat: no-repeat;
}

.popup__close-button:hover {
  opacity: 0.6;
  transition: 0.3s;
}

@media screen and (max-width: 670px) {
  .popup__close-button {
    right: 0px;
    top: -34px;
    width: 20px;
    height: 20px;
    background-size: 20px 20px;
  }
}

.popup__close-button_preview {
  right: -50px;
  top: -40px;
}

@media screen and (max-width: 670px) {
  .popup__close-button_preview {
    right: 0px;
    top: -40px;
  }
}

.popup__container {
  max-width: 430px;
  max-height: 440px;

  width: 100%;
  background: #ffffff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  position: relative;
  text-align: -webkit-center;
}

@media screen and (max-width: 670px) {
  .popup__container {
    max-width: 282px;
    max-height: 322px;
  }
}

.popup__container-preview {
  position: relative;
}

.popup__container_type_delete-confirm {
  max-height: 180px;
}

.popup__container_type_avatar {
  max-height: 270px;
}

.popup__input {
  border: none;
  outline: none;
  padding: 0px;
  margin: 0px 24px 30px 24px;
  max-width: 358px;
  width: 100%;
  min-height: 27px;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

@media screen and (max-width: 670px) {
  .popup__input {
    max-width: 238px;
    width: 100%;
    margin-left: 12px;
    margin-right: 12px;
  }
}

.popup__input_type_post{
  outline: 1px solid rgba(0, 0, 0, 0.2);
  height: 100px;
  border-radius: 5px;
  padding: 5px;
}

.popup__input_type_error {
  border-bottom: 1px solid red;
}

.popup__save-button {
  width: 358px;
  height: 50px;
  margin: 18px 24px 37px;
  padding: 0;
  background: #000000;
  border: 1px solid #000000;
  border-radius: 2px;
  color: white;
  cursor: pointer;
  transition: 0.3s;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
}

.popup__save-button:not([disabled]):hover {
  opacity: 0.8;
  transition: 0.3s;
  background: #000000;
  color: white;
}

.popup__save-button:disabled {

  background: #af2020;

}


@media screen and (max-width: 670px) {
  .popup__save-button {
    width: 238px;
    margin-left: 12px;
    margin-right: 12px;
    font-size: 14px;
    line-height: 17px;
    margin-bottom: 25px;
  }
}

.popup__save-button_inactive {
  opacity: 0.2;
  color: #000000;
  background: none;
}

.popup__title {
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  padding-top: 32px;
  margin: 0px 24px 54px 24px;
}

@media screen and (max-width: 670px) {
  .popup__title {
    margin: 0px 22px 75px 22px;
    padding-top: 25px;
    font-size: 18px;
    line-height: 22px;
  }
}

.popup__title-delete-confirm {
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  padding-top: 34px;
  margin: 0px 36px 20px 36px;
}

@media screen and (max-width: 670px) {
  .popup__title-delete-confirm {
    margin: 0px 22px 25px 22px;
    padding-top: 25px;
    font-size: 18px;
    line-height: 22px;
  }
}

.popup_opened {
  visibility: visible;
  opacity: 1;
}

.popup__subtitle {
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: #FFFFFF;
  margin-top: 10px;
}

.popup__preview {
  max-width: 75vw;
  max-height: 75vh;
  height: 100%;
  width: 100%;
  object-fit: contain;
}

.popup_type_preview {
  background-color: rgba(0, 0, 0, 0.9);
}

.popup__input-error {
  max-width: 358px;
  width: 100%;
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: #ff0000;
  position: absolute;
  margin-top: 30px;
  left: 36px;
  opacity: 0;
}

.popup__input-error_active {
  opacity: 100%;
}

.popup__tooltip-image{
    width: 120px;
    height: 120px;
    background-position: center;
    background-repeat: cover;
    margin: 60px 0 32px;

}
.popup__tooltip-text {
    font-style: normal;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
